import React,{useState} from 'react';
import ReactDOM from 'react-dom';
import './style.css';
import info from '../../images/info.jpg';

const FormApply = props =>{
    
    const continueFn = () => {
        const box1 = document.querySelector('.section1').getAttribute('data-rel');
        const box2 = document.querySelector('.section2').getAttribute('data-rel');
        const box3 = document.querySelector('.section3').getAttribute('data-rel');
        const box4 = document.querySelector('.section4').getAttribute('data-rel');
        const box5 = document.querySelector('.section5').getAttribute('data-rel');

        if(box1 == "active") {
            if(document.querySelector('#propertyName').value == ""){
                alert("Enter property name");
            }else if(document.querySelector('#propertyType').value == ""){
                alert("Select property type");
            }else if(document.querySelector('#noOfUnits').value == ""){
                alert("Select number of units");
            }else if(document.querySelector('#propertyAddress').value == ""){
                alert("Enter property address");
            }else if(document.querySelector('#fileAttachment').value == ""){
                alert("Upload document");
            }else{
                document.querySelector('.section1').removeAttribute('data-rel');
                document.querySelector('.section2').setAttribute('data-rel','active');
            }
        }
        if(box2 == "active") {
            if(document.querySelector('#propertyName1').value == ""){
                alert("Enter property name");
            }else if(document.querySelector('#propertyType1').value == ""){
                alert("Select property type");
            }else if(document.querySelector('#noOfUnits1').value == ""){
                alert("Select number of units");
            }else if(document.querySelector('#propertyAddress1').value == ""){
                alert("Enter property address");
            }else if(document.querySelector('#fileAttachment1').value == ""){
                alert("Upload document");
            }else{
                document.querySelector('.section2').removeAttribute('data-rel');
                document.querySelector('.section3').setAttribute('data-rel','active');
            }
        }
        if(box3 == "active") {
            if(document.querySelector('#propertyName2').value == ""){
                alert("Enter property name");
            }else if(document.querySelector('#propertyType2').value == ""){
                alert("Select property type");
            }else if(document.querySelector('#noOfUnits2').value == ""){
                alert("Select number of units");
            }else if(document.querySelector('#propertyAddress2').value == ""){
                alert("Enter property address");
            }else if(document.querySelector('#fileAttachment2').value == ""){
                alert("Upload document");
            }else{
                document.querySelector('.section3').removeAttribute('data-rel');
                document.querySelector('.section4').setAttribute('data-rel','active');
            }
        }
        if(box4 == "active") {
            if(document.querySelector('#propertyName3').value == ""){
                alert("Enter property name");
            }else if(document.querySelector('#propertyType3').value == ""){
                alert("Select property type");
            }else if(document.querySelector('#noOfUnits3').value == ""){
                alert("Select number of units");
            }else if(document.querySelector('#propertyAddress3').value == ""){
                alert("Enter property address");
            }else if(document.querySelector('#fileAttachment3').value == ""){
                alert("Upload document");
            }else{
                document.querySelector('.section4').removeAttribute('data-rel');
                document.querySelector('.section5').setAttribute('data-rel','active');
            }
        }
        if(box5 == "active") {
            if(document.querySelector('#propertyName4').value == ""){
                alert("Enter property name");
            }else if(document.querySelector('#propertyType4').value == ""){
                alert("Select property type");
            }else if(document.querySelector('#noOfUnits4').value == ""){
                alert("Select number of units");
            }else if(document.querySelector('#propertyAddress4').value == ""){
                alert("Enter property address");
            }else if(document.querySelector('#fileAttachment4').value == ""){
                alert("Upload document");
            }else{
                document.querySelector('.section5').removeAttribute('data-rel');
            }
        }
    }
    const backFn = () => {
        const box1 = document.querySelector('.section1').getAttribute('data-rel');
        const box2 = document.querySelector('.section2').getAttribute('data-rel');
        const box3 = document.querySelector('.section3').getAttribute('data-rel');
        const box4 = document.querySelector('.section4').getAttribute('data-rel');
        const box5 = document.querySelector('.section5').getAttribute('data-rel');

       
        if(box2 == "active") {
            document.querySelector('.section2').removeAttribute('data-rel');
            document.querySelector('.section1').setAttribute('data-rel','active');
        }
        if(box3 == "active") {
            document.querySelector('.section3').removeAttribute('data-rel');
            document.querySelector('.section2').setAttribute('data-rel','active');
        }
        if(box4 == "active") {
            document.querySelector('.section4').removeAttribute('data-rel');
            document.querySelector('.section3').setAttribute('data-rel','active');
        }
        if(box5 == "active") {
            document.querySelector('.section5').removeAttribute('data-rel');
            document.querySelector('.section4').setAttribute('data-rel','active');
        }
    }
    
    return(
        <div>
            <div className="formCont">
                <ul className="mainTab">
                    <li className="activeTab">
                        <p>1</p>
                        <p>Borrower Company Info</p>
                    </li>
                    <li>
                        <p>2</p>
                        <p>Director Info</p>
                    </li>
                    <li>
                        <p>3</p>
                        <p>Financial Info</p>
                    </li>
                    <li>
                        <p>4</p>
                        <p>Past Performance Details</p>
                    </li>
                    <li>
                        <p>5</p>
                        <p>Document Upload</p>
                    </li>
                </ul>
            </div>
            <div className="formContainer">
                <form className="loanApply">
                    <div className="section1 formSec" data-rel="active">
                        <h2>Borrower Company Info</h2>
                        <div className="inputField">
                            <label>Property Name <img src={info} alt="info" className="info" /></label>
                            <input type="text" id="propertyName" placeholder="Property Name" />
                        </div>
                        <div className="inputField">
                            <label>Property Type</label>
                            <select id="propertyType">
                                <option value="">Property Type</option>
                                <option value="Own House">Own House</option>
                                <option value="Rented">Rented</option>
                            </select>
                        </div>
                        <div className="inputField">
                            <label>Number of Units</label>
                            <select id="noOfUnits">
                                <option value="">Number of Units</option>
                                <option value="1">1</option>
                                <option value="2">2</option>
                            </select>
                        </div>
                        <div className="inputField">
                            <label>Property Address</label>
                            <textarea type="text" id="propertyAddress" placeholder="Property Address" />
                        </div>
                        <div className="inputField">
                            <label>File Attachment</label>
                            <input type="file" id="fileAttachment" placeholder="Property Name" />
                        </div>
                    </div>
                    <div className="section2 formSec">
                        <h2>Director Info</h2>
                        <div className="inputField">
                            <label>Property Name <img src={info} alt="info" className="info" /></label>
                            <input type="text" id="propertyName1" placeholder="Property Name" />
                        </div>
                        <div className="inputField">
                            <label>Property Type</label>
                            <select id="propertyType1">
                                <option value="">Property Type</option>
                                <option value="Own House">Own House</option>
                                <option value="Rented">Rented</option>
                            </select>
                        </div>
                        <div className="inputField">
                            <label>Number of Units</label>
                            <select id="noOfUnits1">
                                <option value="">Number of Units</option>
                                <option value="1">1</option>
                                <option value="2">2</option>
                            </select>
                        </div>
                        <div className="inputField">
                            <label>Property Address</label>
                            <textarea type="text" id="propertyAddress1" placeholder="Property Address" />
                        </div>
                        <div className="inputField">
                            <label>File Attachment</label>
                            <input type="file" id="fileAttachment1" placeholder="Property Name" />
                        </div>
                    </div>
                    <div className="section3 formSec">
                        <h2>Financial Info</h2>
                        <div className="inputField">
                            <label>Property Name <img src={info} alt="info" className="info" /></label>
                            <input type="text" id="propertyName2" placeholder="Property Name" />
                        </div>
                        <div className="inputField">
                            <label>Property Type</label>
                            <select id="propertyType2">
                                <option value="">Property Type</option>
                                <option value="Own House">Own House</option>
                                <option value="Rented">Rented</option>
                            </select>
                        </div>
                        <div className="inputField">
                            <label>Number of Units</label>
                            <select id="noOfUnits2">
                                <option value="">Number of Units</option>
                                <option value="1">1</option>
                                <option value="2">2</option>
                            </select>
                        </div>
                        <div className="inputField">
                            <label>Property Address</label>
                            <textarea type="text" id="propertyAddress2" placeholder="Property Address" />
                        </div>
                        <div className="inputField">
                            <label>File Attachment</label>
                            <input type="file" id="fileAttachment2" placeholder="Property Name" />
                        </div>
                    </div>
                    <div className="section4 formSec">
                        <h2>Past Performance Details</h2>
                        <div className="inputField">
                            <label>Property Name <img src={info} alt="info" className="info" /></label>
                            <input type="text" id="propertyName3" placeholder="Property Name" />
                        </div>
                        <div className="inputField">
                            <label>Property Type</label>
                            <select id="propertyType3">
                                <option value="">Property Type</option>
                                <option value="Own House">Own House</option>
                                <option value="Rented">Rented</option>
                            </select>
                        </div>
                        <div className="inputField">
                            <label>Number of Units</label>
                            <select id="noOfUnits3">
                                <option value="">Number of Units</option>
                                <option value="1">1</option>
                                <option value="2">2</option>
                            </select>
                        </div>
                        <div className="inputField">
                            <label>Property Address</label>
                            <textarea type="text" id="propertyAddress3" placeholder="Property Address" />
                        </div>
                        <div className="inputField">
                            <label>File Attachment</label>
                            <input type="file" id="fileAttachment3" placeholder="Property Name" />
                        </div>
                    </div>
                    <div className="section5 formSec">
                        <h2>Document Upload</h2>
                        <div className="inputField">
                            <label>Property Name <img src={info} alt="info" className="info" /></label>
                            <input type="text" id="propertyName4" placeholder="Property Name" />
                        </div>
                        <div className="inputField">
                            <label>Property Type</label>
                            <select id="propertyType4">
                                <option value="">Property Type</option>
                                <option value="Own House">Own House</option>
                                <option value="Rented">Rented</option>
                            </select>
                        </div>
                        <div className="inputField">
                            <label>Number of Units</label>
                            <select id="noOfUnits4">
                                <option value="">Number of Units</option>
                                <option value="1">1</option>
                                <option value="2">2</option>
                            </select>
                        </div>
                        <div className="inputField">
                            <label>Property Address</label>
                            <textarea type="text" id="propertyAddress4" placeholder="Property Address" />
                        </div>
                        <div className="inputField">
                            <label>File Attachment</label>
                            <input type="file" id="fileAttachment4" placeholder="Property Name" />
                        </div>
                    </div>
                    <div className="buttonSec">
                        <button type="button" className="backBtn" onClick={backFn}>Back</button>
                        <button type="button" className="continueBtn" onClick={continueFn}>Continue</button>
                    </div>
                </form>
            </div>
        </div>
    );
}

export default FormApply;