import React,{useState} from 'react';
import logo from '../../images/logo.jpg';
import dropdownIcon from '../../images/arrow.png';
import menuicon from '../../images/menu-icon.jpg';
import './style.css';

const Header = props =>{
    return(
        <header className="header">
            <nav>
                <a href="#"><img src={logo} alt="Logo" className="logo" /></a>
                <div className="userProfile">
                    <p>GC</p>
                    <ul className="profile">
                        <li>Gregory Clark <img src={dropdownIcon} alt="dropdownIcon" className="dropdownIcon" />
                            <ul className="profileDropdown">
                                <li>Edit Profile</li>
                                <li>Sign Out</li>
                            </ul>
                        </li>
                    </ul>
                </div>
            </nav>
            <div className="mobileShow">
                <img src={menuicon} alt="menuicon" className="menuicon" />
            </div>
        </header>
    );
}

export default Header;